<script>
import BackerHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { getItem, removeItem } from '../../common/js/storage'
import cos from '../../common/js/cos'
import { mapState } from 'vuex'
import cloneDeep from '../../common/js/clone-deep'
import { post } from '../../common/js/http'
import location from '../../common/js/address'

export default {
  name: 'seller-goods-edit',
  components: {
    BackerHeader,
    Scroll
  },
  data() {
    return {
      titleImgs: [],
      upType: 'title',
      detailImgs: [],
      colors: [],
      sizes: [],
      checked: true,
      goodsClassify: {},
      rules: [],
      imgIndex: 0,
      form: {
        title: '',
        desc: '',
        delivery: '',
        freight: 0
      },
      address: '',
      columns: location,
      showPicker: false
    }
  },
  created() {
    this.init()
    this.getDetail()
  },
  activated() {
    this.init()
    window.addEventListener('resize', this.scrollRefresh)
  },
  mounted() {
    window.addEventListener('resize', this.scrollRefresh)
  },
  deactivated() {
    window.removeEventListener('resize', this.scrollRefresh)
  },
  computed: {
    ...mapState(['appConfig', 'routeHistory'])
  },
  methods: {
    getDetail() {
      post('/shop/goods/getGoodsInfo', {
        goodsid: this.$route.params.id
      })
        .then(({ data: { data } }) => {
          this.form.title = data.goods.title
          this.form.desc = data.goods.desc
          this.titleImgs = data.goods.thumb_urls.split(',')
          this.detailImgs = data.goods.desc_img_urls.split(',')
          const colorMap = {}
          data.colors.forEach(item => {
            colorMap[item.id] = item.color
          })
          const sizeMap = {}
          data.sizes.forEach(item => {
            sizeMap[item.id] = item.size
          })
          this.rules = data.inventory.map(item => {
            return {
              ...item,
              color: colorMap[item.colorid],
              size: sizeMap[item.sizeid],
              price: parseInt(item.price).toString()
            }
          })
        })
    },
    inputBlur () {
      const u = navigator.userAgent
      const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
      if (isIOS) {
        setTimeout(() => {
          const scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0
          window.scrollTo(0, Math.max(scrollHeight - 1, 0))
        }, 200)
      }
    },
    scrollRefresh() {
      this.$refs.scroll.refresh()
    },
    onConfirm(val) {
      this.address = val.join('')
      this.showPicker = false
    },
    init() {
      const classify = getItem('selectClassify')
      const address = getItem('selectAddress')
      if (classify) {
        this.goodsClassify = classify
        removeItem('selectClassify')
      }
      if (address) {
        this.address = address
        removeItem('selectAddress')
      }
    },
    delTitleImgs(index) {
      this.titleImgs.splice(index, 1)
    },
    delDetailImgs(index) {
      this.detailImgs.splice(index, 1)
    },
    delColor(index) {
      const rules = cloneDeep(this.rules)
      for (let i = 0; i < rules.length; i++) {
        if (rules[i].color === this.colors[index].color) {
          rules.splice(i, 1)
          i--
        }
      }
      this.rules = rules
      this.colors.splice(index, 1)
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    delSize(index) {
      const rules = cloneDeep(this.rules)
      for (let i = 0; i < rules.length; i++) {
        if (this.sizes.length === 1) {
          rules[i].size = ''
        } else {
          if (rules[i].size === this.sizes[index].size) {
            rules.splice(i, 1)
            i--
          }
        }
      }
      // this.genRules()
      this.rules = rules
      this.sizes.splice(index, 1)
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    clickFile(type, index = 0) {
      this.upType = type
      this.imgIndex = index
      this.$refs.file.click()
    },
    editColor(index) {
      this.$set(this.colors, index, {
        ...this.colors[index],
        isConfirm: false
      })
    },
    pushColor() {
      this.colors.push({
        key: Date.now(),
        img_url: '',
        isConfirm: '',
        color: '',
        prev: ''
      })
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    pushSize() {
      this.sizes.push({
        key: Date.now(),
        size: '',
        isConfirm: '',
        prev: ''
      })
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    confirmColor(index) {
      if (!this.colors[index].color) {
        this.$toast.fail('请输入颜色分类')
        return
      }
      for (let i = 0; i < this.colors.length; i++) {
        if (i === index) continue
        if (this.colors[i].color === this.colors[index].color) {
          this.$toast.fail('颜色分类不能重复')
          return
        }
      }
      this.genRules(this.colors[index], 'color', this.colors[index].prev)
      this.$set(this.colors, index, {
        ...this.colors[index],
        isConfirm: true,
        prev: this.colors[index].color
      })
    },
    confirmSize(index) {
      if (!this.sizes[index].size) {
        this.$toast.fail('请输入尺码分类')
        return
      }
      for (let i = 0; i < this.sizes.length; i++) {
        if (i === index) continue
        if (this.sizes[i].size === this.sizes[index].size) {
          this.$toast.fail('尺码分类不能重复')
          return
        }
      }
      this.genRules(this.sizes[index], 'size', this.sizes[index].prev)
      this.$set(this.sizes, index, {
        ...this.sizes[index],
        isConfirm: true,
        prev: this.sizes[index].size
      })
    },
    genRules(current, type, prev) {
      const rules = cloneDeep(this.rules)
      if (prev) {
        rules.forEach(item => {
          if (type === 'size' && item.size === prev) {
            item.size = current.size
          }
          if (type === 'color' && item.color === prev) {
            item.color = current.color
          }
        })
        this.rules = rules
        return
      }
      if (type === 'color') {
        const sizes = this.sizes.filter(item => item.isConfirm)
        if (!sizes.length) {
          rules.push({
            color: current.color,
            size: '',
            left_count: '',
            price: ''
          })
        } else {
          sizes.forEach(size => {
            rules.push({
              color: current.color,
              size: size.size,
              left_count: '',
              price: ''
            })
          })
        }
      } else {
        const colors = this.colors.filter(item => item.isConfirm)
        if (!colors.length) return
        const repeat = rules.map(item => {
          if (!item.size) {
            item.size = current.size
            return item.color
          } else {
            return ''
          }
        })
        colors.forEach(color => {
          if (repeat.indexOf(color.color) >= 0) return
          rules.push({
            color: color.color,
            size: current.size,
            left_count: '',
            price: ''
          })
        })
      }
      this.rules = rules
    },
    upload() {
      const file = this.$refs.file.files[0]
      if (!file) return
      this.cosImg(file)
        .then(url => {
          switch (this.upType) {
            case 'title':
              this.titleImgs.push(url)
              break
            case 'detail':
              this.detailImgs.push(url)
              break
            case 'color':
              this.$set(this.colors, this.imgIndex, {
                ...this.colors[this.imgIndex],
                img_url: url
              })
              break
          }
          this.$refs.file.value = ''
        })
    },
    cosImg(file) {
      const loading = this.$toast.loading({
        message: '上传中...'
      })
      return new Promise((resolve, reject) => {
        const cosObj = cos()
        cosObj.putObject({
          Bucket: this.appConfig.cos_bucket,
          Region: this.appConfig.cos_region,
          Key: this.appConfig.cos_folder_image + `/${file.name}`,
          Body: file
        }, (err, data) => {
          if (err) {
            this.$toast.fail('上传失败')
            reject(err)
            return
          }
          loading.close()
          resolve('https://' + data.Location)
        })
      })
    },
    submit() {
      let msg = ''
      this.rules.some(item => {
        if (!item.price) {
          if (!msg) {
            msg = '有价格未输入'
          }
        }
        if (item.price.indexOf('.') >= 0) {
          if (!msg) {
            msg = '价格不能有小数'
          }
        }
        if (!item.left_count) {
          if (!msg) {
            msg = '有库存未输入'
          }
        }
      })
      // if (!this.form.delivery && !msg) {
      //   msg = '请输入发货周期'
      // }
      // if (!this.address && !msg) {
      //   msg = '请输入发货地'
      // }
      // if (!this.checked && !this.form.freight && !msg) {
      //   msg = '请输入运费'
      // }
      if (msg) {
        this.$toast.fail(msg)
        return
      }
      post('/shop/goods/editGoodsInventory', {
        inventorys: this.rules,
        goodsid: this.$route.params.id
      })
        .then(() => {
          this.$router.back()
        })
        .catch(err => {
          console.log(err)
          // if (err.message === '发布成功，请等待管理员审核') {
          //   this.$router.goBack()
          // }
        })
    }
  },
  watch: {
    routeHistory(newVal) {
      if (newVal.indexOf('/seller/goods/add') < 0) {
        this.$destroy()
      }
    }
  }
}
</script>

